$(function() {
  String.prototype.format = function() {
    var args = arguments;
    return this.replace(/{(\d+)}/g, function(match, number) {
        return typeof args[number] != 'undefined' ? args[number] : match;
    });
  } ;

  $.fn.ShowBanner = function() {
    const dfdBlock = $.Deferred();

    $.getJSON('data/banner.json', function(resp) {
      let strHtml = `
        <div>
          <div class="banner_img" style="background-image: url({0})">
        </div>
      `;

      $.each(resp, function(idx, objResp) {
        $('.banner').append(strHtml.format(objResp.image)) ;
      });

      dfdBlock.resolve();
    });

    return dfdBlock;
  };

  $.fn.ShowTab = function() {
    const dfdBlock = $.Deferred();

    $.getJSON('data/tab.json', function(resp) {
      let strHtml = `
        <div class="tab_box">
          <div>
            <img src="{0}" alt="{1}">
            <p>{2}</p>
          </div>
        </div>
      `;

      $.each(resp, function(idx, objResp) {
        $('.tab_list').append('<li data-num={0}>{1}</li>'.format(
          idx,
          'Tab' + objResp.id
        ));

        $('.tab_body').append(strHtml.format(
          objResp.image,
          'Tab' + objResp.id,
          objResp.content
        )) ;
      });

      dfdBlock.resolve();
    });

    return dfdBlock;
  };

  $.fn.ShowAd = function() {
    const dfdBlock = $.Deferred();

    $.getJSON('data/auto.json', function(resp) {
      let strHtml = `
        <div>
          <div class="gap">
            <img src="{0}" alt="{1}">
          </div>
        </div>
      `;

      $.each(resp, function(idx, objResp) {
        $('.auto_body').append(strHtml.format(
          objResp.image + '?random=' + idx,
          'Auto' + objResp.id
        )) ;
      });

      dfdBlock.resolve();
    });

    return dfdBlock;
  };

  $.fn.ShowRanking = function() {
    const dfdBlock = $.Deferred();

    $.getJSON('data/ranking.json', function(resp) {
      let strHtml = `
        <li>
          <h3>{0}. {1}</h3>
          <img src="{2}" alt="{3}">
        </li>
      `;

      $.each(resp, function(idx, objResp) {
        $('.ranking_list').append(strHtml.format(
          objResp.id,
          objResp.name,
          objResp.image + '?random=' + idx,
          objResp.name
        )) ;
      });

      dfdBlock.resolve();
    });

    return dfdBlock;
  };

  $.fn.ShowSearch = function(strKeyword) {
    const dfdBlock = $.Deferred();

    $.getJSON('https://tcgbusfs.blob.core.windows.net/blobtcmsv/TCMSV_alldesc.json', function(resp) {
      let strHtml = `
        <div>
          <div class="gap">
            <div class="search_info">
              <h3>{0}</h3>
              <p class="detail">{1}</p>
              <p class="area">{2}</p>
              <p class="address">{3}</p>
              <p class="service">{4}</p>
              <p class="pay">{5}</p>
            </div>
          </div>
        </div>
      `;

      if (resp.data && resp.data.park) {
        let arrSearchData = resp.data.park.slice(0, 50);

        if ($.trim(strKeyword) !== '') {
          arrSearchData = arrSearchData.filter((data) => data.area === strKeyword);
        }
        
        // clear
        $('.search_result').html('');

        $.each(arrSearchData, function(idx, objResp) {
          $('.search_result').append(strHtml.format(
            objResp.name,
            objResp.summary,
            objResp.area,
            objResp.address,
            objResp.serviceTime,
            objResp.payex,
          ));
        });
      }

      dfdBlock.resolve();
    });

    return dfdBlock;
  };

  $.fn.ShowMember = function(strKeyword) {
    const dfdBlock = $.Deferred();

    $.ajax('https://randomuser.me/api/?results=12')
      .done(function(resp) {
        $(document).data('MemberPic', []);

        let strHtml = `
          <div>
            <div class="gap">
              <div class="card">
                <div class="card_box">
                  <img src={0} alt={1} />
                  <h3>{1}</h3>
                  <ul class="contact">{2}{3}{4}{5}</ul>
                </div>
              </div>
            </div>
          </div>
        `;
        let strListHtml = `
          <li>
            <span><span><i class="material-icons">{0}</i></span></span>
            <a href="{1}">{2}</a>
          </li>
        `;

        if (resp.results && resp.results.length > 0) {
          $.each(resp.results, function(idx, objResp) {
            $(document).data('MemberPic')[idx] = objResp.picture;

            const strAddress = '{0} {1} {2} {3}'.format(
              objResp.location.city,
              objResp.location.state,
              objResp.location.country,
              objResp.location.postcode
            );

            $('.content_container').append(strHtml.format(
              objResp.picture.large,
              '{0} {1}'.format(objResp.name.first, objResp.name.last),
              strListHtml.format('email', 'mailto:' + objResp.email, objResp.email),
              strListHtml.format('call', 'tel:' + objResp.phone, objResp.phone),
              strListHtml.format('phone_iphone', 'tel:' + objResp.cell, objResp.cell),
              strListHtml.format('home', 'https://www.google.com.tw/maps/search/' + strAddress, strAddress)
            ));
          });
        }

        dfdBlock.resolve();
      })

    return dfdBlock;
  };

  $.fn.ShowAlbum1 = function(strKeyword) {
    const dfdBlock = $.Deferred();

    $.getJSON('data/album1.json', function(resp) {
      let strLargeHtml = `<div><img src={0} alt={1} /></div>`;
      let strListHtml  = `
        <div>
          <div class="gap">
            <img src={0} alt={1} />
          </div>
        </div>
      `;

      $.each(resp, function(idx, objResp) {
        $('.album_large').append(strLargeHtml.format(
          objResp.image,
          'image_' + objResp.id
        ));

        $('.album_list').append(strListHtml.format(
          objResp.image,
          'image_' + objResp.id
        ));
      });

      dfdBlock.resolve(resp);
    })

    return dfdBlock;
  };

  $.fn.ShowAlbum2 = function(strKeyword) {
    const dfdBlock = $.Deferred();

    $.getJSON('data/album2.json', function(resp) {
      let strHtml = `
        <div>
          <div class="gap">
            <div class="album_box">
              <img src={0} alt={1} />
            </div>
          </div>
        </div>  
      `;

      $.each(resp, function(idx, objResp) {
        $('#album2 .album_content').append(strHtml.format(
          objResp.image,
          'image_' + objResp.id
        ));
      });

      dfdBlock.resolve();
    })

    return dfdBlock;
  };

  $(document)
    .on('click', '.tab_list > li', function(e) {
      const intIdx = $(this).attr('data-num');

      $('.tab_list > li').removeClass('active');
      $('.tab_box').removeClass('active');

      $(this).addClass('active');
      $('.tab_box').eq(intIdx).addClass('active');
    })
    .on('click', '.menu_toggle', function(e) {
      if ($(this).hasClass('active')) {
        $(this).removeClass('active');
        $('.menu_list').removeClass('active');
        $('body').removeClass('fixed_body');
      }
      else {
        $(this).addClass('active');
        $('.menu_list').addClass('active');
        $('body').addClass('fixed_body');
      }
    })
    .on('click', 'input[name="photo"]', function(e) {
      const elContainer = $('.content_container > div');
      const strPicSize  = $(this).val();

      for(let i = 0; i < elContainer.length; i++) {
        elContainer.eq(i).find('.card_box > img').attr('src', $(document).data('MemberPic')[i][strPicSize]);
      }
    })
    .on('click', '#album2 .album_content div', function(e) {
      const strPicSrc = $(this).find('img').attr('src');
      const strPicAlt = $(this).find('img').attr('alt');

      $('.modal_body > div')
        .html('')
        .append('<img src={0} alt={1} />'.format(strPicSrc, strPicAlt));
      
      $('.modal_container').show();
      $('body').addClass('fixed_body');
    })
    .on('click', '.go_top', function(e) {
      $("body, html").animate({scrollTop: 0}, 800);
    })
    .on('click', '#modal_btn_close, .modal_container', function() {
      $('.modal_container').hide();
      $('body').removeClass('fixed_body');
    })
    .on('mouseover', '.ranking_list > li', function(e) {
      if ($(window).width() >= 992) {
        $('.ranking_list > li > img').hide();

        $(this).find('img').show();
      }
    })
    .on('change', '#searchParking', function(e) {
      const strVal = $(this).val();
      const strKeyword = strVal !== '全部' ? strVal : '';

      $(document).ShowSearch(strKeyword);
    })
    .on('_showHome', function(e) {
      $(document).ShowBanner()
        .done(function() {
          $('.banner')
            .slick({
              dots: true,
              arrows: false,
              autoplay: true,
              autoplaySpeed: 3000,
              fade: true,
            });
        })
        .fail(function() {
          $('.banner').hide();
        });
  
      $(document).ShowTab()
        .done(function() {
          $('.tab_list > li:eq(0)').addClass('active');
          $('.tab_box:eq(0)').addClass('active');
        });
      
      $(document).ShowAd()
        .done(function() {
          const intShowNum = 4;
          const intTotal   = $('.auto_body > div').length;
          const intPage    = intTotal / intShowNum;
  
          for (let i = 0; i < intShowNum; i++) {
            $('.auto_body > div').eq(i).show();
          }
  
          let intIdx = 0;
  
          setInterval(function() {
            $('.auto_body > div').hide();
  
            if (intIdx >= intTotal) {
              intIdx = 0;
            }
  
            for (let i = intIdx; i < (intIdx + intShowNum); i++) {
              $('.auto_body > div').eq(i).show();
            }
  
            intIdx = intIdx + intShowNum;
          }, 3000);
        });
  
      $(document).ShowRanking()
        .done(function() {
          if ($(window).width() >= 992) {
            $('.ranking_list > li > img').hide();
            $('.ranking_list > li:eq(0) > img').show();
          }
          else {
            $('.ranking_list > li > img').show();
          }
        });
    })
    .on('_showSearch', function(e) {
      $(document).ShowSearch();
    })
    .on('_showMember', function(e) {
      $(document).ShowMember()
        .done(function() {
          $('input[name="photo"][value="large"]').trigger('click');
        });
    })
    .on('_showAlbum', function(e) {
      $(document).ShowAlbum1()
        .done(function(resp) {
          $(document).trigger('_showAlbumSlider');
        });
      
      $(document).ShowAlbum2();
    })
    .on('_showAlbumSlider', function(e) {
      $('.album_large').slick({
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: false,
        fade: true,
        asNavFor: '.album_list'
      });

      $('.album_list')
        .slick({
          autoplay: true,
          infinite: true,
          slidesToShow: 4,
          slidesToScroll: 1,
          arrows: true,
          asNavFor: '.album_large',
          focusOnSelect: true,
          responsive: [
            {
              breakpoint: 767,
              settings: {
                arrows: false,
                slidesToShow: 2
              }
            }
          ]
        });
    })
    .ready(function() {
      $(document)
        .trigger('_showHome')
        .trigger('_showSearch')
        .trigger('_showMember')
        .trigger('_showAlbum');

      $('.go_top').hide();
    });
  
  $(window)
    .on('resize', function() {
      const intWindowWidth = $(window).width();

      if (intWindowWidth >= 992) {
        $('.ranking_list > li > img').hide();
        $('.ranking_list > li:eq(0) > img').show();
      }
      else {
        $('.ranking_list > li > img').show();
        $('.go_top').hide();
      }
    })
    .on('scroll', function(){
      if ($(window).width() >= 992) {
        if($(this).scrollTop() > 100){
          $('.go_top').fadeIn(1000);
        }
        else{
          $('.go_top').fadeOut(1000);
        }
      }
    });
});